#root {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;
  overflow: hidden;

  background: black;

  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
}

.App canvas {
  width: 100%;
  height: 100%;
}

.THREE {
  position: absolute;
  width: 100%;
  height: 100%;
  filter: url(#filter);
}

@keyframes blobs{
  0% {
    color: hsl(200, 40%, 80%);
    font-size: 20vmin;
  }
  50% {
    color: hsl(260, 40%, 80%);
    font-size: 22vmin;
  }
  100% {
    color: hsl(200, 40%, 80%);
    font-size: 20vmin;
  }
}

.LOADING {
  font-size: 20vmin;
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: blobs 4s linear infinite;
}

.sc-player {
  z-index: 999;
  /*width: 100vw;
  height: 10vh;*/
  position: absolute;
  bottom: 0;
  left: 0;
  display: none;
}

#turbFilter {
/*  mix-blend-mode: multiply;*/
  z-index: -1;
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
/*  filter: url(#filter);*/
}

#text {
  z-index: -1;
  top: 50%;
  left: 50%;
  position: absolute;
  width: 50%;
  height: 50%;
  font-size: 200px;
  transform: translate(-50%, -50%);
}

.small {
  position: absolute;
  top: 0;
  left: 0;
      font: italic 300px sans-serif;
    }
